import { initialize } from "./walletAdapter/index.js";
import { createLazySolanaWalletAdapter } from "./createLazySolanaWalletAdapter.js";

// Create the singleton instance that will be used throughout the application
export const singletonSolanaWalletAdapter = createLazySolanaWalletAdapter();

// Register the lazy wallet adapter so that it is discoverable using the solana wallet standard
// This makes it available to wallet selector UIs
initialize(singletonSolanaWalletAdapter);
